h1 {
  font-size: 18pt;
}

h2 {
  font-size: 16pt;
}

h3 {
  font-size: 14pt;
}

h4 {
  font-size: 12pt;
}

body {
  font-size: 10pt;
}

a:hover {
  color: #002147;
  text-decoration: underline;
}

a {
  color: #014493;
  text-decoration: none;
}

h1,
h2,
h3 {
  line-height: 40px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 10px 0;
  font-family: inherit;
  font-weight: bold;
  color: inherit;
}

* {
  box-sizing: border-box;
}

#left_pane {
  min-height: 100vh;
  padding: 50px 20px 20px 20px;
  flex: 0 0 auto;
  background-color: #f5f5f5;
  position: relative;
}

#left_pane_resizer {
  background-color: #f0f0f0;
  cursor: grab;
  position: absolute;
  cursor: pointer;
  top: 0;
  right: 0;
  bottom: 0;
  width: 5px;
  z-index: 11;
}

#main_pane {
  padding: 50px 20px 20px 20px;
  flex: 1 1 auto;
  background-color: white;
  z-index: 10;
}

ol {
  padding-bottom: 5px;
}

li > p {
  margin: 0;
}

li {
  margin: 5px 0 5px 0;
}

/* Viz Styling... */
.node {
  font-family: Arial;
}

.lowkey {
  color: #aaa;
}

.error {
  color: red;
  font-weight: bold;
}

